.todo-list {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-gap: 20px;
}
.todo-list a {
  background-color: #fff;
  padding: 16px;
  border-radius: 6px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.05);
  text-decoration: none;
  color: inherit;
}
.todo-list h4 {
  font-size: 0.9em;
  color: var(--heading-color);
  margin-bottom: 10px;
}
.todo-list p {
  color: var(--text-color);
  font-size: 0.9em;
}
.todo-list .assigned-to {
  margin-top: 20px;
  padding-top: 10px;
  border-top: 1px solid #eee;
}
.todo-list ul {
  margin: 10px 0px;
  display: flex;
}
.todo-list li {
  margin-right: 10px;
}
.todo-list .profile-photo {
  width: 50px;
  height: 50px;
  margin-left: 3px;
}
.name {
  font-size: 15px;
  text-align: center;
}
